import * as React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { wrapper, header, navLinks, navLink, navItem, navBrand, content, sidebar, footer } from './layout.module.css'
import Title from './emotionals/Title'
import DarkLink from './emotionals/DarkLink'
import ShallowH1 from './emotionals/ShallowH1'

const Layout = ({ pageTitle, children }) => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  return (
    <div className={wrapper}>
      <header className={header}>
        <div className={navBrand}>
          <Title>{data.site.siteMetadata.title}</Title>
        </div>
        <ul className={navLinks}>
          <li className={navItem}>
            <DarkLink className={navLink} to="/">
              Home
            </DarkLink>
          </li>
          <li className={navItem}>
            <DarkLink className={navLink} to="/almanaque">
              Almanaque
            </DarkLink>
          </li>
          <li className={navItem}>
            <DarkLink className={navLink} to="/blog">
              Blog
            </DarkLink>
          </li>
          <li className={navItem}>
            <DarkLink className={navLink} to="/about">
              About
            </DarkLink>
          </li>
        </ul>
      </header>
      <main className={content}>
        {pageTitle !== 'nada' ? <ShallowH1> {pageTitle} </ShallowH1> : null}
        {children}
      </main>
      <div className={sidebar}></div>
      <div className={footer}></div>
    </div>
  )
}

export default Layout
